import React from 'react'
import styled from 'styled-components'

import { FONT, BORDER_RADIUS } from 'styles/tokens'
import { splitTagByPrefix } from 'utils/tags'

const StyledButton = styled.button`
  position: relative;
  padding: 0.25em 0.5em;
  margin: 0.125em 0.25em;
  border-radius: ${BORDER_RADIUS.S};
  font-size: ${FONT.SIZE.XXS};
  /* background: hsla(200, 100%, 50%, 0.1); */

  &:before {
    content: '';
    display: block;
    position: absolute;
    background: var(--tertiary-color);
    border-radius: ${BORDER_RADIUS.S};
    opacity: 0.5;
    inset: 0;
  }

  > * {
    position: relative;
  }
`

interface Props {
  tag: string
}

const Tag:React.FC<Props> = ({tag}) => {
  const [prefix, name] = splitTagByPrefix(tag)
  if (prefix === 'loc') return null
  if (prefix === 'form') return null
  if (prefix === 'book') return null // book-club
  if (name === '') return null


  return (
  <StyledButton>
      <span>#{name}</span>
    </StyledButton>
)
  }

export default Tag
