import React from 'react'
import styled from 'styled-components'
import { Link, navigate } from 'gatsby'
import Img from 'gatsby-image'

import H from 'components/H'
import StarRating from 'components/StarRating'
import ArrowIcon from 'components/icons/ArrowIcon'
import { FONT, COLOR, BORDER_RADIUS } from 'styles/tokens'

import Tag from './Tag'

const StyledWrapper = styled.div`
  display: grid;
  grid-template-columns: 2fr 3fr;
  grid-gap: 1em;
  min-height: 300px;
  --book-pages-color: #afbdbb;
`

const StyledBookCard = styled(Link)`
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: 0;
  height: 100%;
  position: relative;
  overflow: hidden;
  background: ${COLOR.BACKGROUND_DARK};
  border-radius: ${BORDER_RADIUS.S};
  outline: 0 !important;
  --perspective: 800px;
`

const StyledImgWrapper = styled.figure`
  max-width: 100%;
  height: auto;
  margin: 0.5em;
  background: var(--book-pages-color);
  box-shadow: 0 0.2em 0.5em rgba(0, 0, 0, 0.1);
  transform: scale(var(--book-scale));

  *::selection {
    background: var(--primary-color);
    color: ${COLOR.BACKGROUND_LIGHT};
  }
`

const StyledImg = styled(Img)`
  vertical-align: bottom;
  transition: transform 0.3s ease-out;
  transform-origin: 0 50%;
  box-shadow: 0 0 0.3em rgba(0, 0, 0, 0.05);
  transform: perspective(var(--perspective)) rotateY(0);

  a:hover &,
  a:focus & {
    transition: transform 0.15s ease-out;
    transform: perspective(var(--perspective)) rotateY(-30deg);
  }

  a:active & {
    transition: transform 0.5s ease-out;
    transform: perspective(var(--perspective)) rotateY(-80deg);
  }
`

const StyledAccessibilityTitle = styled.figcaption`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: ${FONT.SIZE.S};
  color: transparent;
  padding: 0.5em;
  display: flex;
  place-items: center;
`

const StyledButtonLink = styled(Link)`
  display: inline-block;
  font-weight: ${FONT.WEIGHT.BOLD};
  padding: 0.25em 0.5em;
  margin: 0 -0.5em;
  transition: transform 150ms linear;

  &::selection {
    background: transparent;
  }
`

interface Props {
  book: BookType
  featured?: boolean
  big?: boolean
  hideDetails?: boolean
  className?: string
}

const BookHighlight: React.FC<Props> = ({ book, className }) => {
  return (
    <StyledWrapper
      style={
        {
          '--primary-color': book.image.colors.vibrant,
          '--secondary-color': book.image.colors.darkVibrant,
          '--tertiary-color': book.image.colors.lightVibrant,
          '--book-pages-color': book.image.colors.muted,
          '--book-scale': ((book.bookHeight || 198) / 220).toFixed(2),
        } as object
      }
    >
      <StyledBookCard
        to={book.slug}
        style={
          {
            background: book.image.colors.lightMuted,
            color: book.image.colors.darkVibrant,
          } as object
        }
        className={className}
        title={`${book.title} by ${book.author}`}
      >
        <StyledImgWrapper>
          <StyledImg
            fixed={book.image.childImageSharp.h250}
            backgroundColor={book.image.colors.muted}
          />
          <StyledAccessibilityTitle>
            <span>
              {book.title} by {book.author}
            </span>
          </StyledAccessibilityTitle>
        </StyledImgWrapper>
      </StyledBookCard>

      <div style={{display: 'flex', flexDirection: 'column', justifyContent: 'space-between'}}>
        <div>
        <H as="h2" size="M" style={{ margin: 0 }}>
          {book.title}
        </H>
        <H as="h3" size="S">
          by {book.author}
        </H>

        <div style={{display: 'flex', flexDirection: 'column', alignItems: 'start', fontSize: FONT.SIZE.XS, marginTop: '1em'}}>
          <div style={{marginBottom: '1em'}}>
          {book.rating7 && <StarRating of7={book.rating7} mini />}
          </div>

          {book.video && (
            <StyledButtonLink to={book.video.slug}>
              Watch review
              <ArrowIcon />
            </StyledButtonLink>
          )}
          {book.dateReviewed && (
            <StyledButtonLink to={book.slug}>
              Read review
              <ArrowIcon />
            </StyledButtonLink>
          )}

        </div>
        </div>

        <div style={{margin: '0 -0.25em'}}>
          {book.tags.map((tag) =>(<Tag key={tag} tag={tag} />))}
          </div>
      </div>
    </StyledWrapper>
  )
}

export default BookHighlight
